.main {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    gap: 3rem;
    background: #f8d41f;
}

.tittle {
    transform: rotate(270deg);
    font-size: 8rem;
    color: white;
}

.pokedex {
    width: 35%;
    height: 90%;
    border-radius: 2rem;
    border: 1rem solid #4d1717;
    background: #f44336;
    position: relative;
    perspective: 75vw;
    transition: 1s transform ease;
}

.pokedex:hover {
    transform: perspective(75vw) translateX(-50%);
}

.pokedex:hover .image-p {
    visibility: visible;
}

.circles-container {
    height: 20%;
    border-radius: 1rem 1rem 0 0;
    width: 100%;
    border-top: rgba(255, 255, 255, 0.2) 2rem solid;
    border-bottom: 1rem solid #4d1717;
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    position: relative;
}

.circles-container::after {
    content: "";
    position: absolute;
    width: 44.5%;
    height: 5rem;
    bottom: -6rem;
    left: 0;
    border-radius: 0 0 0.7rem 0;
    border-bottom: 1rem solid #4d1717;
    box-shadow: 0rem 10px 0 rgba(255, 255, 255, 0.17);
    background-color: #f44336;
    z-index: 2;
}

.circles-container::before {
    content: "";
    position: absolute;
    width: 7rem;
    height: 5rem;
    right: 13rem;
    bottom: -4.1rem;
    background-color: #f44336;
    border-bottom: solid 1rem #4d1717;
    transform: rotate(-45deg);
    z-index: 3;
    background-clip: padding-box;
}

.circles-section {
    position: relative;
    top: -1.3rem;
    border-radius: 1rem 1rem 0 0;
    height: 100%;
    width: 100%;
    display: flex;
    padding: 1rem 2rem;
    gap: 1rem;
    background: #f44336;
    align-items: flex-start;
    z-index: 10;
}

.circles-section .circle1,
.circles-section .circle2,
.circles-section .circle3,
.circles-section .circle4 {
    width: 2rem;
    border: 0.3rem solid #4d1717;
    height: 2rem;
    border-radius: 50%;
    background-color: #29a9f8;
}

.circles-section .circle1 {
    width: 5rem;
    height: 5rem;
    border: 0.5rem solid #4d1717;
}

.circles-section .circle2 {
    background-color: rgb(255, 0, 0);
}

.circles-section .circle3 {
    background-color: yellow;
}

.circles-section .circle4 {
    background-color: greenyellow;
}

.door-section {
    width: 100%;
    height: 80%;
    display: flex;
    justify-content: space-between;
    border-right: 1rem solid #4d1717;
    align-items: center;
    flex-direction: column;
    background-clip: padding-box;
    transform-style: preserve-3d;
    background: #f44336;
    position: absolute;
    border-radius: 0 0 0 1rem;
    z-index: 1;
}

.display {
    width: 95%;
    height: 95%;
    background: #dddfde;
    clip-path: polygon(100% 0, 100% 100%, 18% 100%, 0 60%, 0 0);
    position: relative;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 0.5rem;
    flex-direction: column;
}

.display2 {
    border-radius: 0.5rem;
    width: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50%;
    transform: translateY(7rem);
    background: #1a1a1a;
    clip-path: polygon(100% 0, 100% 100%, 18% 100%, 0 60%, 0 0);
    position: relative;
}

.display-glass {
    width: 70%;
    height: 60%;
    background-color: lightskyblue;
    border: 0.2rem solid #1a1a1a;
    border-radius: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image-p {
    visibility: hidden;
    width: 50%;
    transition: 1s visibility 1s ease;
}

.dots {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 1rem;
}

.dots span {
    width: 0.7rem;
    height: 0.7rem;
    background: #f44336;
    border-radius: 50%;
    border: 0.1rem solid black;
}

.buttons-apart {
    width: 70%;
    height: 20%;
    display: flex;
    justify-content: space-between;
    transform: translateY(-0.5rem);
    padding: 0.5rem;
    align-items: center;
}

.btn {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    background-color: #f44336;
    border: 0.2rem solid #1a1a1a;
}

.speaker {
    width: 2rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 6px;
}

.speaker span {
    background: #1a1a1a;
    width: 100%;
    border-radius: 0.2rem;
    height: 33%;
}

.controllers-section {
    width: 90%;
    margin-bottom: 1rem;
    height: 25%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.round-btn {
    width: 6rem;
    height: 6rem;
    background: #404040;
    border-radius: 50%;
    border: 0.5rem solid #1a1a1a;
    box-shadow: 0 -1px 20px inset rgba(0, 0, 0, 0.2);
}

.middle-section {
    width: 55%;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.two-btns {
    width: 80%;
    height: 20%;
    display: flex;
    gap: 1rem;
}

.two-btns span {
    width: 50%;
    border-radius: 1rem;
    height: 100%;
    border: 0.3rem solid #1a1a1a;
    background: #f44336;
}

.two-btns span:last-child {
    background: #52ae5e;
}

.green-display {
    border: 10px solid white;
    cursor: pointer;
    display: flex;
    justify-content: center;
    font-family: "Courier New", Courier, monospace;
    color: #1a1a1a;
    padding: 1rem;
    align-items: center;
    border: 0.3rem solid #1a1a1a;
    background: #52ae5e;
    border-radius: 1rem;
    box-shadow: 0 -2px 10px inset rgba(255, 255, 255, 0.6);
}

.green-display h1 {
    animation: 1s blink linear infinite;
}

@keyframes blink {
    50% {
        opacity: 0.5;
    }
}

.plus-btn2 {
    clip-path: polygon(
            0 30%,
            30% 30%,
            30% 0,
            70% 0,
            70% 30%,
            100% 30%,
            100% 70%,
            70% 70%,
            70% 100%,
            30% 100%,
            30% 70%,
            0 70%
    );
    width: 6rem;
    height: 6rem;
    background: #1a1a1a;
    display: flex;
    justify-content: center;
    align-items: center;
}

.plus-btn {
    clip-path: polygon(
            10% 40%,
            40% 40%,
            40% 10%,
            60% 10%,
            60% 40%,
            90% 40%,
            90% 60%,
            60% 60%,
            60% 90%,
            40% 90%,
            40% 60%,
            10% 60%
    );
    width: 100%;
    height: 100%;
    background: #595959;
    position: relative;
}

.plus-btn::after {
    position: absolute;
    content: "";
    border-radius: 50%;
    width: 20%;
    height: 20%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #3f3f3f;
}

.cover {
    width: 100%;
    height: 100%;
    position: absolute;
}

.front::before {
    content: "";
    width: 50%;
    height: 20%;
    clip-path: polygon(37% 0, 100% 0, 100% 100%, -6% 100%);
    position: absolute;
    background-color: #f44336;
    right: 2.8rem;
    top: 0;
}

.front {
    transform-style: preserve-3d;
    height: 80%;
    width: 100%;
    background-color: transparent;
    display: flex;
    z-index: 3;
    justify-content: center;
    align-items: center;
    transition: 1.5s ease-in-out;
    transform-origin: right;
    position: absolute;
}

.pokedex:hover .front,
.pokedex:hover .back {
    transform: rotateY(180deg);
    opacity: 1;
}

.back::before {
    content: "";
    width: 50%;
    height: 20%;
    clip-path: polygon(37% 0, 100% 0, 100% 100%, -6% 100%);
    position: absolute;
    background-color: #4d1717;
    right: 2.8rem;
    top: 0;
}

.back::after {
    content: "";
    width: 50%;
    height: 20%;
    clip-path: polygon(40% 0, 100% 0, 100% 100%, -4% 100%);
    position: absolute;
    background-color: #f44336;
    right: 2.8rem;
    top: 1rem;
}

.back {
    transform-style: preserve-3d;
    height: 80%;
    width: 100%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3;
    transition: 0.01s opacity 0.74s, 1.5s transform ease-in-out;
    transform-origin: right;
    border-bottom: 1rem solid #4d1717;
    border-radius: 0 0 0 1rem;
    position: absolute;
    opacity: 0;
}

.command-container {
    width: 100%;
    padding: 2rem 0 1rem 0;
    border-left: 1rem solid #4d1717;
    border-top: 1rem solid #4d1717;
    background-color: #f44336;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}

.display-back {
    width: 80%;
    height: 30%;
    padding: 1rem;
    background: #1a1a1a;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.5rem solid black;
    border-radius: 0.5rem;
}

.display-back h1 {
    color: #fff;
    font-family: "Courier New", Courier, monospace;
    transform: rotateY(180deg);
}

.btns-back {
    width: 80%;
    height: 30%;
    background: #29a9f8;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    border: 0.2rem solid black;
    border-radius: 0.5rem;
}

.btns-back i {
    width: 20%;
    height: 50%;
    border: 0.1rem solid black;
}

.mid-btns {
    width: 80%;
    height: 5%;
    transform: translateY(-0.2rem);
    display: flex;
    justify-content: flex-start;
    gap: 1rem;
}

.mid-btns span {
    background: #1a1a1a;
    width: 20%;
    border: #000 solid 0.2rem;
    border-radius: 0.5rem;
    height: 100%;
}

.controlls-back {
    width: 80%;
    height: 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
    padding: 0 0 0 1rem;
}

.controlls-back .divisor {
    width: 48%;
    height: 100%;
    display: flex;
    transform: translateY(-0.5rem);
    justify-content: center;
}

.controlls-back .divisor span {
    width: 50%;
    background: #dddfde;
    border: 0.15rem solid #000;
    height: 100%;
}

.controlls-back .divisor span:first-child {
    border-radius: 0.4rem 0 0 0.4rem;
    border-right: transparent;
}

.controlls-back .divisor span:last-child {
    border-radius: 0 0.4rem 0.4rem 0;
}

.btn-divisor {
    width: 2.5rem;
    border-radius: 50%;
    height: 2.5rem;
    border: 0.2rem solid #000;
    background: yellow;
}

.btns-down-back {
    width: 80%;
    height: 10%;
    display: flex;
    gap: 1rem;
}

.btns-down-back span {
    width: 50%;
    border-radius: 0.5rem;
    background: #1a1a1a;
    border: 0.2rem solid #000;
    height: 100%;
}

.rectangle {
    width: 50%;
    height: 45%;
    border-radius: 50%;
    background-color: #1a1a1a;
}

.door-container {
    transform-style: preserve-3d;
    transform: translateY(46px);
    width: 100%;
    height: 82.6%;

    background: #f44336;
    display: flex;
    border-radius: 0 0 0 1rem;
    align-items: center;
}

.triangle {
    margin-left: 3rem;
    border-radius: 0.5rem;
    border: 3rem solid transparent;
    border-left: 5rem solid #4d1717;
    position: relative;
}

.triangle2 {
    position: absolute;
    left: -4.2rem;
    top: -1.5rem;
    border: 1.5rem solid transparent;
    border-left: 3rem solid yellow;
}

.footer-rectangle {
    position: absolute;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    width: 15rem;
    border-radius: 1rem;
    height: 2rem;
    border: 0.5rem solid #4d1717;
    background-color: #a62b26;
}

.hinges-section {
    background-clip: padding-box;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 10%;
    border-left: 1rem solid #4d1717;
    background: #f44336;
    align-items: center;
}

.hinges-section span:first-child {
    width: 100%;
    height: 15%;
    background-color: #a62b26;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.hinges-section span:last-child {
    width: 100%;
    height: 15%;
    background-color: #a62b26;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.hinges-section .hinge1,
.hinges-section .hinge2 {
    width: 90%;
    height: 55%;
    background-color: #4d1717;
    position: relative;
}

.hinges-section .hinge1::before,
.hinges-section .hinge2::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 70%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #f05750;
}
