body {
    background-color: #eff7f6;
}

.home-container {
    display: flex;
    align-items: center;
    justify-content: center;

}

.pokemons-container {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
    padding: 1rem;
    margin-top: 8rem;
    margin-bottom: 1rem;
}

.pagination-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3rem;
}

.selection {
    height: 80%;
    width: 5%;
    background: transparent;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 0.5rem;
}

.selection .btn.next,
.selection .btn.prev {
    background: #e63946;
    /* background-color: rgb(81, 141, 252);*/
    color: white;
    opacity: 1;
}

.selection .btn {
    border-radius: 0.3rem;
    flex: 1 0 60%;
    height: 100%;
    background-color: #eff7f6;
    border: none;
    opacity: 0.5;
    transition: transform 0.2s ease;
    cursor: pointer;
}

.selection .btn.active {
    background: #e63946;
    /*background-color: rgb(81, 141, 252);*/
    color: white;
    opacity: 1;
}

.selection .btn:hover {
    transform: scale(1.08);
    opacity: 1;
}
